<script>
  import api from 'src/packages/Api'
  import Loading from 'partials/Loading'
  
  export default {
    name: 'update-faq',
    data () {
      return {
        faq: {},
        id: 0,
        title: '',
        content: '',
        order: '',
        active: false,

        loaded: false
      }
    },
    components: {
      Loading
    },
    
    async created () {
      try {
        const request = await api.get(`faqs/${this.$route.params.id}`)

        this.faq = request.data
        this.id = request.data.id
        this.title = request.data.title
        this.content = request.data.content
        this.sort = request.data.sort
        this.active = request.data.active ? true : false

        this.loaded = true
      } catch(error) {
        this.$store.dispatch('addToastMessage',
          { text: this.$t(`errors.${error.data.status_code}`) }
        )
        this.$router.push({ name: 'faqs' })
      }
    },
    methods: {
      update () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            const data = {
              title: this.title,              
              content: this.content,
              sort: this.sort,
              active: this.active
            }

            api.put(`faqs/${this.id}`, data).then(response => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('faq.updated'), type: 'success' }
              )
              this.$router.push({ name: 'faqs' })
            }).catch(error => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('errors.any'), error }
              )
            })
          }
        })
      }
    }
  }
</script>

<template>
  <loading v-if="!loaded"/>
  <div class="form-elements" v-else>
    <div class="row">
      <div class="col-md-12">
        <vuestic-widget :headerText="'menu.faqs.update' | translate">

          <div class="row row-reverse">
            <div class="col">
              <div class="row row-reverse">
                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('title')}">
                    <div class="input-group">
                      <input
                        id="title"
                        name="title"
                        v-model="title"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('title')"></i>
                      <label class="control-label" for="title">{{'faq.title'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('title')"
                             class="help text-danger">{{
                        errors.first('title')
                        }}
                      </small>
                    </div>
                  </div>
                </div>

                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('sort')}">
                    <div class="input-group">
                      <input
                        id="sort"
                        name="sort"
                        v-model="sort"
                        v-validate="'required'"
                        type="number"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('sort')"></i>
                      <label class="control-label" for="sort">{{'faq.sort'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('sort')"
                             class="help text-danger">{{
                        errors.first('sort')
                        }}
                      </small>
                    </div>
                  </div>
                </div>

                <div class="col">
                  <vuestic-switch v-model="active">
                    <span slot="falseTitle">
                      {{'custom.inactive' | translate}}
                    </span>
                    <span slot="trueTitle">
                      {{'custom.active' | translate}}
                    </span>
                  </vuestic-switch>   
                </div>
              </div>
            </div>
          </div>
          <div class="row row-reverse">
            <div class="col">
              <textarea 
                id="content"
                class="col-md-12"
                v-model="content"
                :placeholder="'faq.content' | translate"
              >
              </textarea>
            </div>
            <div class="col">
            </div>
            <div class="col">
            </div>
          </div>

          <div class="row">
            <div class="col col text-center mt-4">
              <button class="btn btn-success"  @click="update">
                {{'buttons.update' | translate}}
              </button>
            </div>
          </div>
        </vuestic-widget>        
      </div>
    </div>
  </div>
</template>
